/*----- RESPONSIVE FORMATTING -----*/
@media only screen and (max-width: 1200px) {
  #start-typing {
    padding: 100px 210px;
  }

  #placeholder {
    left: 210px;
  }

  .date {
    left: 20px;
  }

  .info-btn {
    right: 20px;
  }
}
@media only screen and (max-width: 900px) {
  #header {
    width: 620px !important;
  }

  h1 {
    font-size: 70px !important;
  }

  #start-typing {
    padding: 100px 100px;
  }

  #placeholder {
    left: 100px;
  }

  .date {
    left: 60px;
  }

  .info-btn {
    right: 60px;
  }

  .info-box {
    width: 600px !important;
  }
  .info-box .image-container {
    margin: 10px 80px 10px 20px !important;
  }
  .info-box .image-container h3 {
    font-size: 38px !important;
    width: 110% !important;
    right: -60px !important;
  }
  .info-box .description p {
    font-size: 15px !important;
  }
  .info-box .row-lined {
    margin: 30px 0px !important;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .info-box .row-lined .before, .info-box .row-lined .after {
    -webkit-flex: 1 1;
            flex: 1 1;
    font-size: 20px !important;
    padding: 20px 0px !important;
  }
  .info-box .row-lined .before {
    border-bottom: 1px solid rgba(232, 222, 203, 0.05);
    border-right: none !important;
  }
}
@media only screen and (max-width: 600px) {
  h1 {
    font-size: 58px !important;
  }

  #start-typing {
    padding: 100px 40px;
    font-size: 22px;
  }

  #placeholder {
    left: 40px;
  }

  .date {
    visibility: hidden;
  }

  .info-btn {
    left: 50%;
