/*----- HEADER -----*/
#header {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 760px;
  height: 120px;
  top: 0px;
  margin: 180px 0px 40px 0px;
  z-index: 20;
  transition: 1s;
}
#header h1 {
  position: absolute;
  font-size: 90px;
  text-align: center;
  width: 100%;
  color: #E8DECB;
  font-family: Ogg;
}
#header #background-photo {
  position: relative;
  width: 300px;
  height: 500px;
  overflow: hidden;
  bottom: 100px;
  z-index: -2;
}
#header #background-photo .gradient-box {
  position: relative;
  width: 100%;
  height: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0) 70%, rgba(0, 0, 0, 0.4) 100%);
  z-index: -10;
}
#header #background-photo #photo {
  position: absolute;
  width: 320px;
  height: 530px;
  z-index: -30;
}
#header #background-photo h2 {
  position: absolute;
  text-align: center;
  font-size: 16px;
  color: #ccc5b8;
  margin: 10px 20px 24px 20px;
  bottom: 0px;
  z-index: 10;
  pointer-events: none;
}
#header .date, #header .info-btn {
  position: absolute;
  bottom: -90px;
  color: #E8DECB;
  opacity: 0.8;
  font-size: 14px;
}
#header .date {
  left: 0px;
}
#header .info-btn {
  right: 0px;
  cursor: pointer;
  transition: 0.1s !important;
}
#header .info-btn:hover {
  opacity: 0.4;
}

/*----- STUFF TO MAKE THE INFO BOX TRANSITION WORK -----*/
.hide-header {
  -webkit-transform: translateY(-50vh);
          transform: translateY(-50vh);
}
.hide-header .title-box {
  -webkit-transform: translateY(-30vh);
          transform: translateY(-30vh);
}
.hide-header .subtitle-box {
  -webkit-transform: translateY(-15vh);
          transform: translateY(-15vh);
}

.title-box {
  position: absolute;
  width: 83%;
  height: 100px;
  transition: 1.2s;
}

.subtitle-box {
  position: absolute;
  height: 100px;
  width: 100%;
  bottom: 0px;
  transition: 1.2s;
}

#input-box {
  position: relative;
  transition: 1.4s;
}

.hide-input {
  -webkit-transform: translateY(80vh);
          transform: translateY(80vh);
  -webkit-animation-name: hide;
          animation-name: hide;
  -webkit-animation-duration: 0.1s;
          animation-duration: 0.1s;
  -webkit-animation-delay: 1.4s;
          animation-delay: 1.4s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes hide {
  0% {
    z-index: 1;
  }
  100% {
    z-index: -60;
  }
}

@keyframes hide {
  0% {
    z-index: 1;
  }
  100% {
    z-index: -60;
  }
}
#placeholder {
  position: absolute;
  top: 100px;
  left: 340px;
  color: #E8DECB;
  opacity: 0.3;
  font-size: 24px;
}

